import { Check } from 'lucide-react';
import PaymentInfinityPay from '@/components/PaymentInfinityPay';

export default function Home() {
  return (
    <main className="min-h-screen flex items-center">
      <section id="preco" className="py-24 w-full bg-white">
        <div className="max-w-4xl mx-auto px-4 text-center">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-900 font-orbitron">Plano Ricardo Team</h1>
          <p className="mt-3 text-lg text-gray-600">Investimento único para sua transformação</p>
          <div className="card-modern p-10 mt-10">
            <div className="text-5xl md:text-6xl font-bold gradient-text font-orbitron">R$ 149,99</div>
            <div className="mt-2 text-gray-600">Pagamento único • Acesso completo • Sem mensalidades</div>
            <div className="grid md:grid-cols-2 gap-6 mt-10 text-left">
              {[
                'Treino personalizado para seu objetivo',
                'Acompanhamento via WhatsApp',
                'Dicas de nutrição e hábitos',
                'Ajustes conforme evolução',
              ].map((txt, i) => (
                <div key={i} className="flex items-center gap-3">
                  <Check className="w-5 h-5 text-green-600" />
                  <span className="text-gray-700 font-medium">{txt}</span>
                </div>
              ))}
            </div>
            <div className="mt-10">
              <a href="#open-signup" className="btn btn-primary btn-lg">Contratar Agora</a>
            </div>
          </div>
        </div>
      </section>
      <PaymentInfinityPay quiet />
    </main>
  );
}